@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:300,600,800&display=swap);
:root {
	--elements: hsl(0, 0%, 100%);
	--background: hsl(0, 0%, 98%);
	--input: hsl(0, 0%, 52%);
	--text: hsl(200, 15%, 8%);
}

[data-theme="dark"] {
	--elements: hsl(209, 23%, 22%);
	--background: hsl(207, 26%, 17%);
	--text: hsl(0, 0%, 100%);
}

body {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	max-height: 100vh;
	background-color: hsl(0, 0%, 98%);
	background-color: var(--background);
	font-family: "Nunito Sans", sans-serif;
	color: hsl(200, 15%, 8%);
	color: var(--text);
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	display: none;
}

::-webkit-input-placeholder {
	color: hsl(200, 15%, 8%);
	color: var(--text);
	opacity: 0.5;
}

::-moz-placeholder {
	color: hsl(200, 15%, 8%);
	color: var(--text);
	opacity: 0.5;
}

:-ms-input-placeholder {
	color: hsl(200, 15%, 8%);
	color: var(--text);
	opacity: 0.5;
}

::-ms-input-placeholder {
	color: hsl(200, 15%, 8%);
	color: var(--text);
	opacity: 0.5;
}

::placeholder {
	color: hsl(200, 15%, 8%);
	color: var(--text);
	opacity: 0.5;
}

.align {
	margin-left: 5%;
	margin-right: 5%;
}

.stronger-font {
	font-weight: 600;
}


.main-nav {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	flex-wrap: nowrap;
	background-color: var(--background);
	box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
	margin-bottom: 3rem;
}

.home-link {
	margin-left: 5%;
}

.home-link a {
	color: var(--text);
}

.theme-mode-wrapper {
	margin-right: 5%;
}

#themeModeToggle {
	visibility: hidden;
}

.theme-icon-wrapper {
	margin-right: 1rem;
}

.theme-label-wrapper {
	cursor: pointer;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.filters {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 3rem;
}

.countries {
	display: grid;
}

@media (max-width: 375px), (min-width: 376px) {
	.countries {
		grid-template-columns: repeat(1, 1fr);
		grid-gap: 2rem;
	}
	.filters {
		flex-direction: column;
	}
	.filter-region-wrapper {
		margin-top: 2rem;
	}
	.search-wrapper {
		width: 90%;
	}
}

@media (min-width: 800px) {
	.countries {
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 5rem;
	}
	.filters {
		flex-direction: row;
	}
	.filter-region-wrapper {
		margin-top: 0;
	}
	.search-wrapper {
		width: 40rem;
	}
}

@media (min-width: 1110px) {
	.countries {
		grid-template-columns: repeat(4, 1fr);
		grid-gap: 5rem;
	}
	.filters {
		flex-direction: row;
	}
	.filter-region-wrapper {
		margin-top: 0;
	}
	.search-wrapper {
		width: 40rem;
	}
}

a {
	text-decoration: none;
}

.search-wrapper {
	background-color: var(--background);
	border: none;
	border-radius: 5px;
	box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	flex-wrap: nowrap;
	min-height: 3rem;
}

#searchCountry {
	border: none;
	font-family: "Nunito Sans";
	font-weight: 600;
	font-size: 14px;
	width: 85%;
	background-color: var(--background);
	color: var(--text);
}

#searchCountry:focus {
	outline: none;
}

.search-wrapper:hover {
	cursor: text;
}

.search-icon-wrapper {
	margin-left: 2rem;
	margin-right: 1rem;
}

.filter-region-wrapper {
	display: flex;
	flex-wrap: nowrap;
	flex-direction: row;
	align-items: center;
	background-color: var(--background);
	border-radius: 5px;
	box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
}

#filterRegion {
	-webkit-appearance: none;
	border: none;
	width: 10rem;
	padding-left: 30px;
	height: 3rem;
	background-color: var(--background);
	color: var(--text);
}

#filterRegion > option {
	border: none;
}

#filterRegion:focus {
	outline: none;
}

.filter-icon-wrapper {
	display: flex;
	margin-right: 1rem;
}

.country-card {
	background-color: var(--background);
	color: var(--text);
	border-radius: 20px;
	box-shadow: 0px 0px 1px 1px rgba(0, 0, 0, 0.2);
	-webkit-transition: -webkit-transform ease-in-out 0.5s;
	transition: -webkit-transform ease-in-out 0.5s;
	transition: transform ease-in-out 0.5s;
	transition: transform ease-in-out 0.5s, -webkit-transform ease-in-out 0.5s;
	height: 25rem;
}

.country-card:hover {
	-webkit-transition: -webkit-transform ease-in-out 0.5s;
	transition: -webkit-transform ease-in-out 0.5s;
	transition: transform ease-in-out 0.5s;
	transition: transform ease-in-out 0.5s, -webkit-transform ease-in-out 0.5s;
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

.country-flag-wrapper {
	width: 100%;
	height: 14rem;
}

.country-flag {
	width: 100%;
	height: 100%;
	object-fit: cover;
	border-top-left-radius: 20px;
	border-top-right-radius: 20px;
}

.country-content {
	padding: 0.5rem 1rem 3rem 2rem;
}

.country-details {
	list-style: none;
	padding: 0;
	text-decoration: none;
}



.back-button-wrapper {
	margin-bottom: 3rem;
}

.back-btn {
	padding: 0.5rem 1rem 0.5rem 1rem;
	border: none;
	background-color: var(--background);
	color: var(--text);
	box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.25);
	cursor: pointer;
	font-size: 14px;
	font-family: "Nunito Sans", sans-serif;
	width: 8rem;
}

.back-btn:focus {
	outline: none;
}

.back-btn svg {
	margin-right: 1rem;
}

.country-details-wrapper {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
}

.flag-wrapper {
	width: 800px;
}

.flag-wrapper img {
	width: 100%;
	box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.45);
}

.country-info-wrapper {
	padding-left: 5rem;
	padding-top: 1rem;
}

.country-info-lists-wrapper {
	display: flex;
	flex-direction: row;
}

.country-info-left,
.country-info-right {
	padding: 0;
	margin: 0;
	list-style-type: none;
}

.country-info-left li,
.country-info-right li {
	margin-bottom: 1rem;
}

.country-info-right {
	margin-left: 4rem;
}

.border-countries-wrapper {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-top: 3rem;
}

.border-countries-btn-list-wrapper {
	display: flex;
	justify-content: flex-start;
}

.country-info-name {
	font-size: 2rem;
}

@media (max-width: 1000px) {
	.country-info-wrapper {
		padding: 0;
	}
	.country-details-wrapper {
		flex-wrap: wrap;
	}
	.country-info-lists-wrapper {
		flex-direction: column;
	}
	.country-info-left,
	.country-info-right {
		text-align: left;
	}
	.country-info-right {
		margin: 0;
		margin-top: 2rem;
	}
	.border-countries-wrapper {
		flex-direction: column;
		align-items: flex-start;
	}
	.border-countries-btn-list-wrapper {
		flex-direction: column;
		margin-bottom: 3rem;
	}
}

.border-countries-btn-list-wrapper button {
	border: none;
	padding: 0.5rem;
	font-family: "Nunito Sans", sans-serif;
	background-color: var(--background);
	color: var(--text);
	box-shadow: 0px 0px 1px 1.5px rgba(0, 0, 0, 0.25);
	cursor: pointer;
	margin-left: 1rem;
}

.border-countries-btn-list-wrapper button:focus {
	outline: none;
}

@media (max-width: 1000px) {
	.border-countries-btn-list-wrapper button {
		margin: 0;
		margin-top: 1rem;
	}
}

